<template>
  <v-container>
    <v-row >
      <v-col class="mb-4" :col="target==null?12:4" :md="target==null?12:4" :lg="target==null?12:4" :xl="target==null?12:4">
        <h1 class="display-2 font-weight-bold mb-3">
          DML pet project
        </h1>

        <p align="left" class="subheading font-weight-regular">
          Чтобы начать, загрузите фотографию.
          <br>Лица должны составлять не менее 10% площади фото.
        </p><br>

        <form 
          style="width:80%"
                    ref="fileform"
                    @drag.prevent="handlerFunction"
                    @dragstart.prevent="handlerFunction"
                    @dragend.prevent="handlerFunction"
                    @dragenter.prevent="handlerFunction"
                    @dragleave.prevent="handlerFunction"
                    @dragover.prevent="handlerFunction"
                    @drop.prevent="handlerFunctionDrop"
                >
                    <div :class="`dnd-zone`">
                        <center>
                        Перетяните файл в выделенную область или нажмите - Выбрать файл.
                        <br>
                            <v-btn
                                color="default"
                                class="mt-2"
                                @click="$refs.inputUpload.click()"
                            >Выбрать файл</v-btn>
                            <br>
                            <span >{{filename}}</span>
                        
                        <input v-show="false" ref="inputUpload" type="file" @change="onFileChange()" accept=".png,.jpg,.jpeg,.gif">
                        </center>
                    </div>
          </form>
          <br>
          <p v-if="err" style="color:brown">{{err}}</p>
          <v-btn
            color="primary"
            @click.native="uploadPhoto"
            :loading="loadingConnection"
          >Отправить</v-btn>
      </v-col>
      <v-col
        v-if="target!=null"
        col="5" md="5" lg="5" xl="5"
        class="mb-5"
        cols="12"
      >
        <v-img
          max-height="500px"
          contain
          :src="'http://localhost:8000'+target.img_url"
        />
      </v-col>
      <v-col
        v-if="target!=null"
        col="3" md="3" lg="3" xl="3"
        class="mb-5"
      >
        <h2>На фото:</h2>
        <br>
        <div v-for="(age, i) in target.predict_age[0]">
          <span><b>{{ages[i].name}} ({{ages[i].diap}})</b>, с вероятностью ~{{Math.round(age*10000)/10000}}<br></span>
        </div>
        <br><hr><br>
        <div v-for="(gender, i) in target.predict_gender[0]">
          <span><b>{{gend[i]}}</b>, с вероятностью ~{{Math.round(gender*10000)/10000}}<br></span>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<style scoped>
.dnd-zone{
    border-radius: 5px 5px;
    border-color: grey !important;
    padding: 15px;
    padding-top: 50px;
    padding-bottom: 50px;
    border: 2px dotted;
}
</style>

<script>
import axios from 'axios';

export default {
  name: 'HelloWorld',
  data: () => ({
    file: false,
    filename: '',
    target: null,
    err: '',
    loadingConnection: false,
    ages: [
      {name: 'Личинус', diap: '0-6'},
      {name: 'Щегол', diap: '7-23'},
      {name: 'Бумер', diap: '25-32'},
      {name: 'Старпёр', diap: '35+'},
    ],
    gend: [
      'Дама',
      'Пацан'
    ]
  }),
  methods: {
    onFileChange() {
      this.file = this.$refs.inputUpload.files[0];
      this.filename = this.file.name; 
      this.err = null;
    },
    async uploadPhoto() {
      this.loadingConnection = true;
      const apiInstance = axios.create({
        baseURL: 'http://localhost:8000',
      });
      let formData = new FormData();
      formData.append('upload', this.file);
      let response;
      try {
        response = await apiInstance.post(`/api/v1.0/upload`, 
          formData,
          {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          }
        );
      } catch (e) {this.err='Сервер вернул ошибку'; this.loadingConnection = false;}
      this.file = false;
      this.filename = 'Выберите файл';
      this.$refs.inputUpload.value = null;
      if (!response.data.error)
        this.target = response.data
      else
        this.err = 'Лицо, не обнаружено!'
      this.loadingConnection = false;
    },
        determineDragAndDropCapable(){
            let div = document.createElement('div');
            let answer = '';
            if (( ( 'draggable' in div )
                    || ( 'ondragstart' in div && 'ondrop' in div ) )
                    && 'FormData' in window
                    && 'FileReader' in window) {
                answer = 'space.tools.imgs.uploadDnd'
            } else {
                answer = 'space.tools.chat.chooseFileDesc'
            }
            return answer;
        },
  },
};
</script>
